.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #175a85;
    z-index: 2000; /* Ensure it appears above the header */
    text-align: right;
    padding-top: 2vh;
    overflow: scroll;
  }
  
  .menu-title {
    font-size: 8vw;
    color: white;
    margin-bottom: 5vh;
    margin-top: 19vh;
    margin-right: 4vw;
    text-align: center;
    padding: 5vw;
  }
  
  .menu-list {
    list-style-type: none;
    padding: 0;
    margin-top: 2vh;
  }
  
  .menu-list li {
    color: white;
    margin-bottom: 3vh;
    border-bottom: 0.2vh solid white;
    padding: 1.5vh 0;
    text-align: center;
    padding-right: 5vw;
    font-size: 5vw;
  }
  
  .closeBtn {
    position: fixed;
    top: 3vh;
    left: 3vw;
    width: 15vw;
  }
  
  .whiteLogo {
    width: 47%;
    height: auto;
    position: absolute;
    top: 7vh;
    left: 25vw;
    padding: 2vw;
  }
  
  .menu-list li.active {
    background-color: white;
    color: #175a85;
  }